.conversation-list-item {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  padding: 10px;
}

.conversation-list-item-sel {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: rgb(0, 188, 212, 0.2);
  
}

.conversation-list-item:hover {
  background: #eeeef1;
  cursor: pointer;
}

.conversation-photo {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
 
}

.conversation-title {
  font-size: 14px;
  font-weight: bold;
  /* text-transform: capitalize; */
  margin: 0;

}

.conversation-snippet {
  font-size: 14px;
  color: #888;
  margin: 0;
  
}

.conversation-new {
  width: 17px;
  height: 17px;
  background-color: green;
  margin-left: 30px;
  border-radius: 50%;
 
}